ound: #ffc653;\n}\n\n.RELEASED {\n  background: #56ca3d;\n}\n\n.RELEASE-FAILED {\n  background: #f04b4b;\n}\n\n.box {\n  background: #ffffff;\n  box-shadow: 3px 4px 6px 0px rgba(63, 110, 243, 0.1);\n  border-radius: 8px;\n  padding: 25px 16px 21px 16px;\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n}\n\n.search {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n\n  .label {\n    font-size: 12px;\n    color: #555555;\n    margin: 0 8px;\n  }\n\n  .part {\n    width: 160px;\n  }\n\n  .part + .part {\n    margin-left: 16px;\n  }\n}\n\n.user-count {\n  grid-area: auto / span 3;\n\n  h4 {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding-right: 24px;\n  }\n\n  .count-num {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    border: 0 !important;\n    width: 100px;\n    height: 248px !important;\n    background: #f4faff;\n\n    p {\n      font-weight: 700;\n      color: #002b60;\n      font-size: 18px;\n    }\n\n    a {\n      font-size: 12px;\n      cursor: pointer;\n      color: #5a9cff;\n      line-height: 20px;\n      height: 20px;\n    }\n  }\n\n  a {\n    font-size: 12px;\n    cursor: pointer;\n    color: #5a9cff;\n    line-height: 16px;\n    height: 16px;\n  }\n\n  .circle {\n    background: #ebeef5;\n    border-radius: 50%;\n    height: 14px;\n    width: 14px;\n    line-height: 14px;\n    text-align: center;\n    font-size: 12px;\n    display: inline-block;\n    margin-left: 8px;\n    color: #879099;\n  }\n\n  .user-t {\n    line-height: 40px !important;\n    background: #f4faff;\n    font-size: 12px;\n    color: #333333;\n    font-weight: 700;\n    position: absolute;\n    left: 0;\n    top: -58px;\n    text-align: center;\n    width: 100%;\n    padding-left: 1px;\n  }\n\n  .c {\n    display: flex;\n    font-size: 12px;\n    padding: 12px !important;\n    align-items: flex-end;\n\n    & > div:nth-child(3) .user-t {\n      border-radius: 8px 0 0 8px;\n    }\n\n    & > div:last-child .user-t {\n      border-radius: 0 8px 8px 0;\n    }\n\n    & > div {\n      position: relative;\n      text-align: center;\n      flex: 1;\n      border-left: 1px solid #e7f1fd;\n      height: 194px;\n\n      p:nth-child(2) {\n        font-weight: 700;\n      }\n\n      p:last-child {\n        margin-bottom: 0;\n      }\n\n      p {\n        margin-bottom: 16px;\n        line-height: 1;\n        text-align: center;\n      }\n    }\n  }\n}\n\n.qrcodemodal {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n}\n:deep(.el-table__row):hover .keyIcon {\n  display: block !important;\n}\n.hideKey {\n  // 过长显示省略号\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n"],"sourceRoot":""}]);
